* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}

html {
  scroll-behavior: auto;
}

body {
  font-family: "Open Sans", sans-serif;
  position: relative;
  overflow-x: hidden;
  cursor: url('https://www.flaticon.com/svg/static/icons/svg/67/67687.svg'), auto,
}

.sectionOne {
  width: 100%;
  position: relative;
  z-index: 2;
  transition: transform 0.5s;
  -webkit-transition: transform 0.5s;

  &-after {
    &::before {
      content: "";
      display: block;
      position: absolute;
      background-image: url("../img/bg-right.png");
      background-repeat: no-repeat;
      background-position: right 100%;
      background-size: 100%;
      z-index: 4;
      right: 0;
      bottom: 0;
      width: 660px;
      height: 100%;
      @include media(1366) {
      }

      @include media(1024) {
      }
    }

    &::after {
      content: "";
      display: block;
      position: absolute;
      background: #FFDE05;
      border-radius: 10px 0 0 10px;
      width: 92%;
      height: 100%;
      right: 0;
      z-index: 3;
      margin-left: auto;
      top: 0;
      @include media(1366) {
        width: 94%;
      }

      @include media(1024) {
        width: 97%;
      }
    }
  }


  &-bg {
    border-radius: 10px 0 0 10px;
    background-image: url("../img/keyboard.png");
    background-repeat: no-repeat;
    background-position: right 70%;
    background-size: 45%;
    z-index: 10;
    display: flex;
    justify-content: center;
    align-items: start;
    flex-direction: column;
    padding: 200px 0 200px 0;
    position: relative;


    @include media(1920) {
      padding: 80px 0 80px 0;
      background-size: 35%;
    }

    @include media(1366) {
      padding: 80px 0 80px 50px;
      background-size: 40%;
    }

    @include media(768) {
      padding: 80px 0 80px 50px;
      background-size: 57%;
    }

    @include media(576) {
      padding: 30px 0 280px 20px;
      background-size: 82%;
      background-position: right 100%;
    }

    &__title {
      font-family: "Bebas Neue", sans-serif;
      font-style: normal;
      font-weight: bold;
      font-size: 68px;
      line-height: 45px;
      margin-bottom: 26px;
      color: black;

      @include media(1024) {
        font-size: 50px;
        line-height: 35px;
      }

      @include media(576) {
        font-size: 40px;
        line-height: 17px;
      }

    }

    &__text {
      font-family: "Proxima Nova", sans-serif;
      font-style: normal;
      font-weight: normal;
      font-size: 20px;
      line-height: 35px;
      max-width: 35%;
      margin-bottom: 35px;
      color: black;
      @include media(1920) {
        max-width: 44%;
      }

      @include media(1366) {
        max-width: 46%;
        font-size: 20px;
        line-height: 30px;
      }
      @include media(768) {
        max-width: 60%;
      }

      @include media(576) {
        max-width: 100%;
        margin-bottom: 20px;
      }
    }

    &__btn {
      background: #000000;
      border-radius: 5px;
      color: white;
      width: 200px;
      height: 70px;
      font-family: "Bebas Neue", sans-serif;
      font-style: normal;
      font-weight: bold;
      font-size: 24px;
      line-height: 50px;
      transition: all 0.2s ease-in-out;
      @include media(1366) {
        width: 160px;
        height: 55px;
        font-size: 20px;
        line-height: 30px;
      }
      @include media(768) {
        width: 140px;
        height: 45px;
        font-size: 19px;
        line-height: 25px;
      }

      &:hover {
        color: #FFDE05;
        transform: scale(1.1);
        box-shadow: 0 0 10px 5px rgba(55, 55, 55, 0.1);
        transition: all 0.2s ease-in-out;
      }
    }

    &__round {
      width: 200px;
      height: 200px;
      position: absolute;
      bottom: -120px;
      left: 20%;
      animation: roundBall 4s ease-in-out;
      @include media(1366) {
        width: 180px;
        height: 180px;
        bottom: -100px;
      }

      @include media(768) {
        width: 140px;
        height: 140px;
        bottom: -70px;
        left: 30%;
      }

      @include media(576) {
        width: 140px;
        height: 140px;
        bottom: -70px;
        left: 60%;
      }
    }

    &__dotted {
      position: absolute;
      bottom: -145px;
      left: 0;
      @include media(576) {
        left: 0;
      }
    }

    &__down {
      width: 50px;
      position: absolute;
      top: 60px;
      z-index: 5;
      cursor: pointer;
      animation: downScroll 3s linear infinite;
    }
  }
}

