.footer {
  border-top: 1px solid rgba(182, 182, 182, 0.3);
  margin-top: 150px;
  padding: 85px 0 0 0;
  position: relative;
  z-index: 2;

  &-left {
    position: relative;
    z-index: 4;
  }

  &-logo {
    margin-right: 20px;
  }

  &-title {
    font-family: "Bebas Neue", sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 22px;
    line-height: 24px;
    letter-spacing: 0.02em;
    color: #000000;
    max-width: 45%;
    @include media(576) {
      max-width: 100%;
    }
  }

  &-block {
    display: flex;
    align-items: center;
    margin-top: 40px;

    &__icons {
      margin: 0 10px;
    }
  }

  &-list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: start;

    &__title {
      font-family: "Bebas Neue", sans-serif;
      font-style: normal;
      font-weight: bold;
      font-size: 20px;
      letter-spacing: 0.05em;
      color: #000000;
      position: relative;
      padding-bottom: 30px;
      @include media(576) {
        font-size: 32px;
      }

      &::after {
        content: "";
        display: block;
        position: absolute;
        width: 80%;
        height: 2px;
        background: #FFDE05;
        bottom: 20px;
        left: 0;
      }
    }

    &__items {
      font-family: "Bebas Neue", sans-serif;
      font-style: normal;
      font-weight: normal;
      font-size: 18px;
      line-height: 26px;
      letter-spacing: 0.05em;
      color: #B8B8B8;
      @include media(576) {
        font-size: 24px;
      }

      &.active {
        color: black;
      }
    }
  }

  &-right {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    &__images {
      position: absolute;
    }
  }

  &-info {
    margin-top: 80px;
    position: relative;
    z-index: 4;
    @include media(768) {
      margin-top: 180px;
    }

    &__title {
      font-family: "Bebas Neue", sans-serif;
      font-style: normal;
      font-weight: bold;
      font-size: 20px;
      letter-spacing: 0.05em;
      color: black;
      margin-bottom: 0;
      @include media(576) {
        font-size: 30px;
      }
    }

    &__text {
      font-family: "Proxima Nova", sans-serif;
      font-style: normal;
      font-weight: 300;
      font-size: 16px;
      line-height: 26px;
      color: black;
      @include media(576) {
        font-size: 18px;
      }
    }
  }

  &-alright {
    margin-top: 40px;
    display: flex;
    justify-content: center;
    position: relative;
    padding: 20px 0;

    &::after {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      width: 96%;
      height: 1px;
      background: rgba(182, 182, 182, 0.3);
    }

    &__text {
      font-family: "Proxima Nova", sans-serif;
      font-style: normal;
      font-weight: normal;
      font-size: 16px;
      line-height: 28px;
      text-align: center;
      color: #B8B8B8;
    }
  }
}

.footer-right .footer-right__images:nth-child(2) {
  z-index: 2;
  right: 100px;
  top: 20px;
}

.footer-right .footer-right__images:nth-child(3) {
  z-index: 3;
  right: 30px;
  top: 20px;
}

