.header-container {
  position: relative;
  z-index: 10;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;

  &-nav {
    display: flex;
    justify-content: end;
    align-items: end;
    @include media(768) {
      display: none;
    }


    &__item {
      font-family: "Bebas Neue", sans-serif;
      font-size: 20px;
      font-weight: bold;
      margin: 0 14px;

      @include media(768) {
        font-size: 30px;
        margin: 20px 0;
        padding-bottom: 10px;
        position: relative;
      }

      a {
        color: #bbbbbb;

        &.active {
          color: black;
          display: flex;
          justify-content: center;
          align-items: center;

          &::after {
            content: "";
            display: block;
            position: absolute;
            width: 6px;
            height: 6px;
            background: #000;
            bottom: 0;
            border-radius: 50%;

          }
        }
      }
    }
  }

  &-mobile {
    display: none;
    position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, 0.91);
    left: -2000px;
    top: 0;
    backdrop-filter: blur(8px);
    z-index: 9999;

    &__bar {
      position: absolute;
      right: 15px;
      top: 3rem;
      margin-right: auto;
      margin-left: auto;

      i {
        font-size: 30px;
      }
    }
  }


  &-bars {
    display: none;
    @include media(768) {
      display: block;
    }

    i {
      font-size: 30px;
    }
  }
}

.container.container-border {
  position: absolute;
  height: 100%;
  left: 0;
  right: 0;
  z-index: 1;
  top: 0;
  bottom: 0;
  padding: 0;
}

.container.container-border .line-effect {
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.container.container-border .line-effect div {
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-box;
  display: -ms-flexbox;
   display: flex;
  -webkit-box-pack: justify;
  -webkit-justify-content: space-between;
  -moz-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  padding: 0 15px;
  height: 100%;
}

.container.container-border .line-effect div .line-cont-1 {
  border-left: 1px solid rgba(194, 194, 194, 0.30);
  width: .1px;
  height: 100%;
  position: relative;

  &::before {
    position: absolute;
    width: 3px;
    height: 33px;
    background-color: rgba(126,135,158,.9);
    content: "";
    top: -40px;
    left: -2px;
    animation: lineEffect linear infinite;
    animation-duration: 30s;
    animation-delay: 5s;
  }
}

